@import url(https://fonts.googleapis.com/css?family=Acme&display=swap);
@import url(https://fonts.googleapis.com/css?family=Acme|Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Acme|Forum|Fredoka+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    font-family: 'Forum', cursive;
}
.cell{position:relative}.empty{background:url(/static/media/pustpole.ff9ee3bc.jpg);background-size:cover}.chicken_inside{background:url(/static/media/kur.ff8e83f9.jpg);background-size:cover}.rye_inside{background:url(/static/media/pshenichnoepole.94db500a.jpg);background-size:cover}.cow_inside{background:url(/static/media/cow.8d5914d4.jpg);background-size:cover}.btn{position:absolute;bottom:0}.hidden{opacity:0}.shown{-webkit-transition:all 0.5s;transition:all 0.5s;opacity:1}

.field{max-width:710px;margin:10px auto;display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:10px;grid-auto-rows:200px;grid-auto-flow:dense}

.value{padding-left:30px;font-style:italic;font-size:24px}.val{font-family:'Fredoka One', cursive}.red{color:#9b1b1b}.green{color:#30ac30}

.household{width:100px;height:100px;position:absolute}.household .btn{opacity:0}.dragged{position:static}.dragged .btn{position:absolute;bottom:95px;opacity:1}.dragged .feed{left:0}.dragged .harvest{right:0}

.dragged_chicken{color:#0b096d;font-size:22px;text-align:center;font-weight:900}.wait_for_drag-chicken{top:230px;left:32px;background-image:url(/static/media/chicken.15cb847a.jpg);background-size:cover}

.wait_for_drag-cow{background:#10cabb;background-image:url(data:image/png;base64,);background-size:cover;top:330px;left:32px}.dragged_cow{color:#6d69e2;font-size:22px;text-align:center;font-weight:900}

.wait_for_drag-rye{top:40px;left:32px;background:hotpink;background-image:url(data:image/jpeg;base64,);background-size:cover}.wait_for_drag-rye .btn{position:absolute;bottom:55px;left:0}.dragged_rye{color:#6d69e2;font-size:22px;text-align:center;font-weight:900}

